import {Fragment, useEffect, useRef} from 'react';
import {Link, useParams} from 'react-router-dom';
import MetaData from '../layout/MetaData';
import Loader from '../layout/Loader';
import Toast from '../Toast';
import {useDispatch, useSelector} from 'react-redux';
import {getOrderDetails, clearErrors} from '../../actions/orderActions';

const OrderDetails = ()=>{
	const toastRef = useRef();
	const dispatch = useDispatch();
	const {id} = useParams();
	const {
		loading,
		error,
		order
	} = useSelector(state=>state.orderDetails);
	const {
		shippingInfo,
		orderItems,
		paymentInfo,
		user,
		totalPrice,
		orderStatus
	} = order || {};

	useEffect(()=>{
		dispatch(getOrderDetails(id));
		if(error){
			toastRef.current.setMessage(error);
			dispatch(clearErrors());
		}
	}, [dispatch, error, id]);

	const shippingDetails = shippingInfo && `${shippingInfo.address}, ${shippingInfo.city}, ${shippingInfo.postalCode}, ${shippingInfo.country}`;
	const isPaid = paymentInfo && paymentInfo.status === 'succeeded' ? true : false;
	
	return(
		<div className="container container-fluid">
			<MetaData totle="Order Details"/>
			<Toast ref={toastRef}/>
			{loading ?
				<Loader/> :
				<Fragment>
					<div className="row d-flex justify-content-between">
					    <div className="col-12 col-lg-8 mt-5 order-details">
					        <h1 className="my-5">Order # {order && order._id}</h1>
					        <h4 className="mb-4">Shipping Info</h4>
					        <p><b>Name:</b> {user && user.name}</p>
					        <p><b>Phone:</b> {shippingInfo && shippingInfo.phoneNo}</p>
					        <p className="mb-4"><b>Address:</b>{shippingDetails}</p>
					        <p><b>Amount:</b> ${totalPrice}</p>
					        <hr />
					        <h4 className="my-4">Payment</h4>
					        <p className={isPaid ? 'greenColor' : 'redColor'}><b>{isPaid ? 'PAID' : 'NOTPAID'}</b></p>
					        <h4 className="my-4">Order Status:</h4>
					        <p className={order && order.orderStatus && String(order.orderStatus).includes('Delivered') ? 'greenColor' : 'redColor'}><b>{orderStatus}</b></p>
					        <h4 className="my-4">Order Items:</h4>
					        <hr />
					        <div className="cart-item my-1">
					        	{orderItems && orderItems.map(item=>
					        		<div key={item.product} className="row my-5">
						                <div className="col-4 col-lg-2">
						                    <img src={item.image} alt={item.name} height="45" width="65" />
						                </div>
						                <div className="col-5 col-lg-5">
						                    <Link to={`/product/${item.product}`}>{item.name}</Link>
						                </div>
						                <div className="col-4 col-lg-2 mt-4 mt-lg-0">
						                    <p>${item.price}</p>
						                </div>
						                <div className="col-4 col-lg-3 mt-4 mt-lg-0">
						                    <p>{item.quantity} Piece(s)</p>
						                </div>
						            </div>
				        		)}
					        </div>
					        <hr />
					    </div>
					</div>
				</Fragment>
			}
		</div>
	);
}

export default OrderDetails;